        title: "Anthony Mackie's MCU Contract Was Originally For 10 movies",
        shortDescription: "Anthony Mackie originally signed up to a 10-picture deal with Marvel Studios. The Marvel Cinematic Universe has been a tremendous success, but it simply wouldn't be possible without multi-picture deals. It requires actors to reprise their roles across multiple films, with character arcs that run over the course of years.",
        fullDescription: "Anthony Mackie originally signed up to a 10-picture deal with Marvel Studios. The Marvel Cinematic Universe has been a tremendous success, but it simply wouldn't be possible without multi-picture deals. It requires actors to reprise their roles across multiple films, with character arcs that run over the course of years.Sooner or later, every contract comes to an end. Marvel recruited many of the OG Avengers under six-film contracts, which became increasingly problematic as the MCU grew. Viewers knew that Avengers: Endgame would be the swan song for the OG Avengers, simply because the contracts had run out. More recently, Marvel's been a lot more secretive about the length of the contracts they've signed actors up with, in order to prevent audiences knowing in advance when an actor was about to bow out. There have been recurring rumors that the contracts have had a quite impressive length, but these have never been confirmed. Mackie's comment gives insight into just how far in advance Marvel Studios likes to operate, and suggests they always had big plans for Sam Wilson. Indeed, it's even possible Marvel had always intended Falcon to become the next Captain America, back when they first hired Mackie for the part. The current wave of Marvel blockbusters appear to be heavily influenced by the comics published between 2010 through to 2014 - Captain Marvel was heavily influenced by Kelly Sue DeConnick's Captain Marvel run, which launched in 2012 - and that was the period where Sam Wilson became Captain America. If so, Marvel Studios president Kevin Feige had clearly always been planning to push themes of diversity and inclusion, which were core to Nick Spencer's Captain America: Sam Wilson comics.",
