        fullDescription: "Avengers: Endgame has yet to pass James Cameron's Avatar at the box office, though it surpassed another Cameron film, Titanic, only a couple of weeks after its release. To best Avatar globally, Avengers: Endgame will have to bring in another $38.4 million - which is possible, but not likely, as Avatar has been outpacing Avengers: Endgame at the box office since its third week of release. Endgame was also available on more screens than Avatar was, making it even more impressive that Avatar has held onto its crown. Per THR, McFeely and Markus will appear in a \"spotlight panel\" celebrating their work on the massive Marvel hit. Their panel is scheduled for Comic-Con's second day, Friday, July 19, and will be moderated by Jeff Goldsmith, who is known for his podcast The Q&A With Jeff Goldsmith. Friday will be an all-Marvel morning in Hall H, as Avengers: Endgame directors Joe and Anthony Russo will also have a panel. That conversation will be led by Steven Weintraub of Collider. In addition to writing Avengers: Endgame, McFeely and Markus also collaborated on all three Captain America films, as well as Thor: The Dark World and Avengers: Infinity War. Their Marvel connection extends off the big screen, too; they also co-created the critical darling Agent Carter for ABC. Outside of the MCU, the two worked on all three Chronicles of Narnia films. McFeely and Markus are beloved by Marvel fans, especially for their work on Captain America: The Winter Solider, and they're also to thank for much of the MCU's impressive continuity across films.",
