        fullDescription: "Fans of Seinfeld can finally experience the show up close and personal: The Seinfeld Experience is coming to New York City. The popular NBC sitcom ran for nine seasons from 1989 to 1998. Created by Larry David and Jerry Seinfeld, it followed the adventures of a fictional Seinfeld and his friends in Manhattan. The show focused on the day-to-day lives of its characters rather than big events. It's thought of as one of the greatest sitcoms of all time and had a large impact on TV comedy as we know it today. Seinfeld was beloved by critics and audience members alike; its final season averaged over 38 million viewers. It also won 10 Primetime Emmy Awards and was nominated for 68 throughout its run. In addition to David and Seinfeld, it starred Jason Alexander, Julia Louis-Dreyfus, and Michael Richards, all of whom have continued to work steadily since the show's conclusion. Louis-Dreyfus starred in The New Adventures of Old Christine and VEEP, while David went on to create and star in Curb Your Enthusiasm. Seinfeld, meanwhile, has found success with Comedians in Cars Getting Coffee, a web series and talk show where he chats with fellow comedians. According to Deadline, The Seinfeld Experience is \"part retail shop and part immersive show promotion\" and features \"interactive exhibits with costumes, memorabilia, set re-creations, and props.\" It will also have unspecified new content and a store that sells \"limited-edition and exclusive merchandise.\" The experience comes from both entertainment producer Superfly and Warner Bros. Consumer Products. Tickets will go on sale in the next few months and will be available until February 2020.",
