        fullDescription: "Amazon Prime will be showcasing The Boys, The Expanse and Carnival Row at this year's San Diego Comic Con event. Although Netflix remains the undisputed leader among the ever-growing selection of streaming services, Amazon Prime has been steadily growing its catalog of original content and are now attracting bigger stars and more sought-after properties. One of Amazon's most hotly anticipated shows currently in development is The Boys, based on the graphic novels by Garth Ennis and Darick Robertson. Starring Karl Urban, The Boys depicts an R-rated world where superheroes have spiraled out of control and only a group of steroid-fueled eccentrics are capable of keeping them violently in check. Initially running on the SyFy network, The Expanse was cancelled after 3 seasons. After backlash from fans, The Expanse was quickly picked up by Amazon, who have made the entire series available for streaming and are set to premiere season 4 later this year. The science fiction series has been widely praised by critics and appears to have found a stable new home. With the star power of Cara Delevingne and Orlando Bloom, Carnival Row is already attracting attention and promises to mix fantasy elements with a neo-noir murder mystery plot. These three shows will form the basis of Amazon Prime's presence at SDCC 2019. Amazon have revealed that there will be immersive, interactive activation experiences inspired by each series, complete with live performers and exclusive parties. Amazon's SDCC lineup also includes a fan premiere of The Boys and a series of panels that will include Bloom, Delevingne and Urban, as well as stars from other Amazon properties such as The Man In The High Castle and Undone. SDCC attendees should be able to find the Amazon Prime Video Experience easily enough by heading towards a giant 40-foot tower with LED screens showing scenes from the platform's upcoming releases.",
