        fullDescription: "Spider-Man: Far From Home director Jon Watts confirms that some major scenes from the trailers have been cut from the final film. The next Spider-Man film is designed as a sort of epilogue to Avengers: Endgame, bringing an end to Marvel's Phase 3. It sees Peter Parker and his school friends head out on a field trip across Europe - one that is swiftly interrupted by Nick Fury, super-spy. The Spider-Man: Far From Home trailers set up the school trip pretty well by establishing Peter's excitement at going on vacation. They included shots of Peter visiting Mr. Delmar's Deli, his picking up his passport (in a delightful tongue-twister moment that surely required some reshoots) and even Spider-Man taking down a group of crooks before bantering with the police. It's disappointing to see these scenes wind up on the editing room floor; they looked quite entertaining, were clearly filled with character moments, and also featured some innovative uses of webbing that have never been seen on the big screen before. For all that's the case, though, in thematic terms the cuts make sense. This Spider-Man film is all about taking Peter Parker out of his natural environment, which means a lengthy sequence back in New York just wouldn't quite fit thematically.",
