        fullDescription: "Franchise creator Aaron Korsh is uncertain about the possibility of a Suits and Pearson crossover. As Suits enter its ninth and final season, a spin-off based on Gina Torres' beloved Jessica Pearson begins next month. Considering the link between the shows, fans are hoping that they see the two worlds collide before the pioneering series goes off the air. Sadly, it doesn't seem like it's in the cards, at least according to Korsh. One of the original cast members of Suits, Torres exited the series in the middle of season 7 for personal reasons. She maintained a great relationship with her former co-workers, however, even popping in a couple of times now just to check Harvey (Gabriel Macht) and the team over at New York. Since her name was taken down from the wall, there's very little that's been revealed about what she's been up to in Chicago - only that the complications of hiring Mike (Patrick J. Adams) continue to haunt her in the Windy City and force her to join the mayor's team. In just a few more weeks, fans will finally learn how she's been coping with her new life, at the same time that her ex-crew in the Big Apple begin wrapping up their story. Speaking with TV Line in light of the back-t0-back premier for Suits season 9 and Pearson, franchise creator Korsh was asked about the possibility of an original character permanently transferring to the spin-off. He's apprehensive about this, at least right now. Instead, he's keener on a crossover through a specific arc. In any case, he's keeping the door open for any narrative opportunity that would allow them to do either of these possibilities.",
