        title: "Titans Face Off In Teen Titans Go! Vs. Teen Titans Crossover movie Trailer",
        shortDescription: "The Teen Titans GO! Vs. Teen Titans trailer is officially released. The upcoming direct-to-video crossover was announced back in October, and brings together both animated versions of the team.",
        fullDescription: "The Teen Titans GO! Vs. Teen Titans trailer is officially released. The upcoming direct-to-video crossover was announced back in October, and brings together both animated versions of the team. gether both animated versions of the team.The original Teen Titans series premiered on Cartoon Network in 2003. The show quickly became a fan favorite, and developed a dedicated following. It blended serious storytelling, humor, and comic book battles all into five seasons. Despite the show being acclaimed, a sixth season was not produced. However, the show was given a sequel movie in 2006 titled, Teen Titans: Trouble in Tokyo. A spin-off show titled Teen Titans GO! premiered in 2013. Although it was met with hostility by longtime fans, the show has since acquired a dedicated following as well for its parody humor. Teen Titans GO! likewise received a feature-length movie. Unlike Trouble in Tokyo, Teen Titans GO! To the movies was released in theaters, and did very well for itself. Now, the serious and comedic are about to collide later this year. The official trailer for Teen Titans GO! Vs. Teen Titans has premiered (via IGN), and it shows how the two teams meet up. The antagonists also appear, and it looks like Teen Titans GO! Robin may have some difficulty working with the other team at first. Check out the trailer below.",
