        title: "Dog the Bounty Hunter's Beth Chapman Passes Away At 51",
        shortDescription: "Dog the Bounty Hunter’s Beth Chapman passed away at the age of 51. After being diagnosed with stage 4 throat cancer, she was initially hospitalized due to breathing difficulties, and was then placed in a medically-induced coma by doctors.",
        fullDescription: "Dog the Bounty Hunter’s Beth Chapman passed away at the age of 51. After being diagnosed with stage 4 throat cancer, she was initially hospitalized due to breathing difficulties, and was then placed in a medically-induced coma by doctors. Beth endured a similar situation in April and was rushed to the hospital, though she was sent home shortly thereafter when the issue cleared up. Beth has experienced a long battle with throat cancer, undergoing aggressive chemotherapy after it returned in November. The couple was set to host a new series, Dog's Most Wanted for WGN America, in which a portion of the show would feature her cancer struggle. Beth said that the show’s air date was pushed back to next year. According to TMZ, Beth was rushed to the hospital due to breathing difficulties, where sources said she remained in a medically-induced coma. After arriving at the hospital on the morning of Saturday, June 22, Beth was placed into a medically-induced coma for her own protection. Upon arriving at the emergency room, she experienced severe pain and suffered from lack of oxygen. As she began to receive treatment, it was deemed appropriate to put her under mild sedation, until finally placing her under a medically-induced coma. Her husband revealed that she passed away via his personal Twitter account. Take a look at his tweet below.",
