        postMonth: 'jun',
        title: "Rick & Morty Season 4 Episode Will Screen At Adult Swim Festival In November",
        shortDescription: "Fans of Adult Swim series Rick and Morty will get their first look at season 4 at a screening this November. The new season of the popular animated series has been a long time coming, with season 3 wrapping back in October 2017. Despite being critically acclaimed, the show wasn't renewed until several months later, when Adult Swim ordered 70 new episodes. The network also announced last month that season 4 will premiere in November, the same month as the screening.",
        fullDescription: "Fans of Adult Swim series Rick and Morty will get their first look at season 4 at a screening this November. The new season of the popular animated series has been a long time coming, with season 3 wrapping back in October 2017. Despite being critically acclaimed, the show wasn't renewed until several months later, when Adult Swim ordered 70 new episodes. The network also announced last month that season 4 will premiere in November, the same month as the screening. Contract issues were to blame for the holdup in renewal, and the massive deal will ensure that Rick and Morty is on the air for many years to come. An episode count for season 4 hasn't been announced yet, but seasons 2 and 3 were 10 episodes each, with season 1 having 11. Leading up to the show's third season finale, co-creator Dan Harmon said he was hoping for a 14 episode fourth season. The third season was initially supposed to be 14 episodes as well, but Harmon and his team worried that the quality of the show would suffer if they went over 10. ",
