export const mainPageFilter = posts.reverse().filter((postId) => {
    return postId.id !== isNaN ? postId : null
})

export const archivePageFilter = posts.reverse().filter((postId) => {
    return postId.id !== isNaN ? postId : null
})

export const movieFilter = posts.reverse().filter((postCategory) => {
   return postCategory.category === 'movie' ? postCategory : null
})

export const tvNewsFilter = posts.filter((postCategory) => {
    return postCategory.category === 'television' ? postCategory : null
})

export const travelFilter = posts.filter((postCategory) => {
    return postCategory.category === 'travel' ? postCategory : null
})
export const fashionFilter = posts.filter((postCategory) => {
    return postCategory.category === 'fashion' ? postCategory : null
})

export const getPostsMap = (array) => (array.reduce((accObj, post) => ({
    ...accObj,
    [post.id]: post
}), {}))

export const scrollPost = () => {
    window.scrollTo({
        top: 0,
        behavior: 'auto'
    })
}

export default posts